import * as React from 'react';
import Container from '@mui/material/Container';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Grid from '@mui/material/Unstable_Grid2';
import TextField from '@mui/material/TextField';

// Icons 
import FormatAlignLeftIcon from '@mui/icons-material/FormatAlignLeft';
import FormatAlignCenterIcon from '@mui/icons-material/FormatAlignCenter';
import FormatAlignRightIcon from '@mui/icons-material/FormatAlignRight';
import FormatAlignJustifyIcon from '@mui/icons-material/FormatAlignJustify';
import { v4 as uuidv4 } from 'uuid';

// Components
import Todo from './Todo';

// Dialog imports ..
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

// Others
import { useTodos, useTodosDispatch } from '../contexts/todosContext';
import { useToast } from "../contexts/ToastContext";
import { useState, useEffect, useMemo, useReducer } from 'react';
import todosReducer from "../reducers/todosReducer";

export default function ToDoList() {
    console.log("Re-render");

    const todos = useTodos();
    const dispatch = useTodosDispatch();
    const { showHideToast } = useToast();
    
    const [dialogTodo, setDialogTodo] = useState("");
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [showUpdateDialog, setShowUpdateDialog] = useState(false);
    const [titleInput, setTitleInput] = useState("");
    const [displayedTodosType, setDisplayedTodosType] = useState("all")


    // Filtration Arrays ..
    // Note: We improve performance by improve code .. 
    const completedTodos = useMemo(() => {
        return todos.filter((t) => {
            console.log("Calling completed todos")
            return t.isCompleted;
        });
    }, [todos]);
    
    const notCompletedTodos = useMemo(() => {
        return todos.filter((t) => {
            console.log("Calling not completed todos")
            return !t.isCompleted;
        });
    }, [todos]);

    let todosToBeRendered = todos;

    if (displayedTodosType == "completed") {
        todosToBeRendered = completedTodos;
    }else if (displayedTodosType == "non-completed")
    {
        todosToBeRendered = notCompletedTodos;
    } else {
        todosToBeRendered = todos;
    }


    useEffect(() => {
        dispatch({ type: "get" });
    }, []);


    // Handlers
    function changeDisplayedType(e) {
        setDisplayedTodosType(e.target.value)
    }

    function handleAddClick() {
        dispatch({type:"added", payload:{newTitle: titleInput}});
        setTitleInput("");
        showHideToast("تمت الاضافة بنجاح");
    }

    function openDeleteDialog(todo) {
        setDialogTodo(todo)
        setShowDeleteDialog(true);
    }
    
    
    function openUpdateDialog(todo) {
        setDialogTodo(todo);
        setShowUpdateDialog(true);
    }

    function handleDeleteDialogClose() {
          setShowDeleteDialog(false);
       }
    
     
    function handleDeleteConfirm() {
        dispatch({type: "deleted", payload: dialogTodo})
        setShowDeleteDialog(false);
        showHideToast("تم الحذف بنجاح")
    }

    
    function handleUpdateClose() {
        setShowUpdateDialog(false);
    }

    
    function handleUpdateConfirm() {
    dispatch({type:"updated", payload: dialogTodo})
    setShowUpdateDialog(false);
    showHideToast("تم التحذيث بنجاح")
}

    
    const todosJsx = todosToBeRendered.map((t) => {
        return (
            <Todo 
            key={t.id}
            todo={t}
            showDelete={openDeleteDialog}
            showUpdate={openUpdateDialog}
            />
        );
    });


    return (
        <>
            {/* Start Delete Dialog */}
            <Dialog
                style={{direction:"rtl"}}
                onClose={handleDeleteDialogClose}
                open={showDeleteDialog}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                {"هل أنت متأكد من حذف المهمـة ؟"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        لا يمكنك التراجـع عن الحذف بعد اتمامـه 

                    </DialogContentText>
                </DialogContent>
                
                <DialogActions>
                    <Button onClick={handleDeleteDialogClose}>إغـــلاق</Button>
                    <Button autoFocus onClick={handleDeleteConfirm}>
                        نعـم، قم بالحـذف
                    </Button>
                </DialogActions>
            </Dialog>
            {/* End Delete Dialog  */}

            {/* Start Update Dialog */}
            <Dialog
                style={{direction:"rtl"}}
                    onClose={handleUpdateClose}
                    open={showUpdateDialog}

                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">
                          تعديل مهمـة
                  </DialogTitle>
                
                    <DialogContent>
                            <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="عنوان المهمـة"
                        fullWidth
                        variant="standard"
                        value={dialogTodo.title}
                        onChange={(e) => {
                            setDialogTodo({
                                ...dialogTodo,
                                title: e.target.value
                            });
                        }}
                            />
                            
                            <TextField
                                autoFocus
                                margin="dense"
                                id="name"
                                label="التفاصيـل"
                                fullWidth
                                variant="standard"
                                value={dialogTodo.details}
                                onChange={(e) => {
                                    setDialogTodo({
                                        ...dialogTodo,
                                        details: e.target.value
                                    });
                        }}
                            />
                    </DialogContent>
                
                    <DialogActions>
                    <Button onClick={handleUpdateClose}>إغـــلاق</Button>
                    <Button autoFocus onClick={handleUpdateConfirm}>
                        تأكيـد التحديث 
                    </Button>
                    </DialogActions>
            </Dialog>
            {/* End update Dialog */}

            <Container maxWidth="sm">
                    <Card
                        sx={{ minWidth: 275 }} style={{
                        maxHeight: "80vh",
                        overflow: "scroll",
                        }}
                    >
                        <CardContent>
                            {/* <Typography></Typography> It's instead <h1> or <h2>, just it's private technic of "MUI", and inside we can define variant="h1 or h2 .." to get fontSize for text we need .. */}
                            <Typography variant="h2" style={{fontWeight: "bold"}}> مهـامي </Typography>
                            <Divider />
                            
                            {/* Start Filter Buttons */}
                            <ToggleButtonGroup
                                style={{direction:"ltr", marginTop:"30px"}}
                                value={displayedTodosType}
                                exclusive
                                onChange={changeDisplayedType}
                                aria-label="text alignment"
                                color = "primary"                        >
                                
                                <ToggleButton value="non-completed" style={{fontWeight:"bold"}}> غـير المنجــز </ToggleButton>
                                <ToggleButton value="completed" style={{fontWeight:"bold"}}> المنجــز </ToggleButton>
                                <ToggleButton value="all" style={{fontWeight:"bold"}}> الكـل </ToggleButton>

                                </ToggleButtonGroup>
                            {/* End Filter Buttons */}

                            {/* Start all Todos */}
                                {todosJsx}
                            {/* End all Todos */}


                            {/* Start Input + Add Button */}
                            <Grid container style={{marginTop:"20px"}} spacing={2}>
                                
                            <Grid xs={4}
                                    display="flex"
                                    justifyContent="space-around"
                                    alignItems="center"
                                >
                                    <Button
                                        style={{ width: "100%", height: "100%" }} variant="contained"
                                        onClick={() => {
                                            handleAddClick()
                                        }}

                                        disabled={titleInput.length == 0}
                                    >
                                        إضافـة
                                    </Button>
                                </Grid>

                                <Grid xs={8}
                                    display="flex"
                                    justifyContent="space-around"
                                    alignItems="center"
                                    // style={{background:"green"}}
                                >
                                    <TextField
                                        style={{width:"100%"}}
                                        id="outlined-basic"
                                        label="عنوان المهمــة"
                                        variant="outlined" 
                                        value={titleInput}
                                        onChange={(e) => {
                                            setTitleInput(e.target.value)
                                        }}
                                        />
                                </Grid>
        
                            </Grid>
                            {/* End Input + Add Button */}

                        </CardContent>
                </Card>
            </Container>
        </>
  );
}